import React, { Component } from 'react'
import {Link,NavLink} from "react-router-dom"
export default class Home extends Component {
   
    pushMovie(){
        // console.log(this.props)
        this.props.history.push("/movie")
    }
    pushFood(){
        this.props.history.push("/food")
    }

    replace(url){
        this.props.history.replace(url)
    }
    render() {
        return (
            <div>
                <h3>1.Link</h3>
                <Link to="/movie">电影</Link>
                <Link to="/food">美食</Link>

                <h3>2.NavLink</h3>
                <NavLink to="/movie">电影</NavLink>
                <NavLink to="/food">美食</NavLink>

                <h3>3.push</h3>
               <button onClick={()=>this.pushMovie()}>push 电影</button>
               <button onClick={()=>this.pushFood()}>push 美食</button>

               <h3>4.replace</h3>
               <button onClick={()=>this.replace('/movie')}>replace 电影</button>
               <button onClick={()=>this.replace('/food')}>replace 美食</button>
            </div>
        )
    }
}
